      <div>
        <p>
          Lika-liku perjalanan menulis skripsi biasa dialami kebanyakan mahasiswa tingkat akhir. Menulis skripsi seperti menghadapi raja terakhir menuju wisuda. Termasuk aku yang saat itu sedang berkutat di depan layar laptop ditemani kursor di layar yang berkedip-kedip bersamaan berlalunya detik waktu. <em>Tik&hellip;tik...tik... </em>
          Kursor itu masih berkedip di akhir paragraf yang tidak aku lanjutkan. Nihil. Tidak ada satupun inspirasi yang muncul di kepalaku. Setiap detik kursor itu muncul di layar, semakin meningkat kecemasanku karena tidak mampu menyelesaikan proposal penelitian ini. Aku merasa terjebak dalam situasi yang membuatku frustasi. Terlebih lagi karena ada harapan besar dari keluargaku untuk aku segera lulus kuliah.
        </p>
        <p>Aku sampaikan kepada dosen pembimbing tentang kebuntuanku menulis. Bahwa aku merasa asing dengan topik skripsi yang kupilih, hingga ketidaktertarikan aku dengan dunia per-skripsi-an ini. Beliau menyarankanku untuk meneliti tentang hal yang aku sukai, seperti tentang LSM, tempat di mana aku menyibukkan diri, yang juga merupakan sebuah dunia baru bagiku. Seseorang biasanya betah menghabiskan banyak waktunya, jika berkutat dengan hal yang ia sukai, seperti aku katanya, yang berkegiatan di LSM Damar selama lebih dari 1 tahun saat itu. Topik awal penelitianku adalah bagaimana peran negara dalam menangani isu perdagangan perempuan pekerja migran yang menjadi korban Tindak Pidana Perdagangan Orang (TPPO). Topikku kemudian berubah dan yang menjadi objek penelitianku adalah LSM Perkumpulan Damar.</p>
        <p>
          <blockquote>Seperti takdir, beberapa bulan kemudian Sely Fitriyani, Direktur Eksekutif Lada Damar, mengundangku hadir dalam zoom meeting program Safe and Fair dari UN Women.</blockquote>
        </p>
        <p>
          Pertemuan ini membahas tentang perempuan pekerja migran yang rentan mengalami kekerasan seperti TPPO yang mirip dengan topik penelitian skripsiku. Aku berpikir program ini tentu akan membantu proses penulisan skripsiku, karena topiknya sangat sesuai dengan penelitian yang sedang aku lakukan. Mengetahui hal ini, aku mengajukan diri untuk berpartisipasi dalam setiap kegiatan program <em>Safe and Fair</em>. Aku hadir sebagai representasi dari Mulead Lampung, sebuah komunitas anak muda yang <em>concern </em>pada isu gender dan konselor sebaya bagi perempuan korban kekerasan.
        </p>
        <p>Menurutku isu pekerja migran bukanlah isu yang menarik di kalangan anak muda. Bukan isu &ldquo;seksi&rdquo; yang menarik untuk dijadikan topik diskusi. Mungkin karena isu ini tidak relevan atau berdampak langsung bagi anak-anak muda di sekitarku. Tapi aku melihat isu pekerja migran khususnya bagi perempuan dan anak perempuan merupakan isu krusial yang berdampak signifikan di tingkat global.</p>
        <p>
          Melalui kemampuan sederhanaku memproduksi media kampanye, aku ikut berpartisipasi dalam penyusunan Rencana Aksi Daerah (RAD) Provinsi Lampung untuk pencegahan dan penanganan TPPO, membentuk kebijakan berupa prosedur operasi standard (<em>Standard Operating Procedure/SOP</em>) mekanisme penanganan kasus dan rujukan korban kekerasan yang dialami pekerja migran oleh lembaga layanan, pertemuan lintas sektor dan anggota gugus tugas, penguatan kapasitas bagi purna PMI, membantu kepulangan perempuan-perempuan pekerja migran yang menjadi korban TPPO, hingga kampanye kreatif dan aksi kolektif untuk meningkatkan kesadaran publik akan isu TPPO yang rentan dialami oleh perempuan pekerja migran.
        </p>
        <p>
          Sebagai mahasiswa yang baru mengenal isu gender, terlibat dalam program <em>Safe and Fair</em> ini membuka pemahamanku bahwa isu gender adalah isu yang sangat kompleks.
        </p>
        <p>
          Bagaimana perjuangan-perjuangan menuju kesetaraan ternyata juga masuk dalam konteks migrasi internasional khususnya bagi mereka yang digelari pahlawan devisa negara, pekerja migran. Seluruh proses itu aku tuangkan ke dalam tulisan yang menjadi karya tulis ilmiah atau skripsiku. Selama 1 tahun, diriku belajar seputar isu-isu krusial khususnya memahami isu TPPO bagi pekerja migran sekaligus berproses menjadi orang dewasa. Sebelumnya aku melihat skripsi adalah proses yang sangat menyulitkan, aku merasa tidak yakin apakah aku mampu melewatinya. Aku melihat teman-temanku berkutat pada buku-buku di perpustakaan dan mengurung diri di kamar dari pagi ke pagi. Proses yang membosankan dan membayangkannya saja aku sudah pusing. Namun menulis skripsi berdasarkan pelaksanaan program <em>Safe and Fair</em>
          membuat perjalananku menjadi lebih berwarna, senang bisa menuliskan temuan-temuan di lapangan dan menjadikannya sebagai data ilmiah yang valid.
        </p>
        <p>
          Berakhirnya program <em>Safe and Fair</em> dan terselesaikannya skripsiku bukanlah akhir dari jalanku terlibat dalam dunia pergerakan. Perjalananku 1 tahun kemarin adalah proses mengumpulkan amunisi sebagai pejuang kesetaraan. Aku juga mengintrospeksi diri yang masih memiliki banyak kekurangan ini, bagaimana belajar mengendalikan keputusasaanku hidup di negeri yang belum menghargai perempuan. Besar harapanku kedepannya untuk semakin dalam memahami isu gender sebagai isu krusial, melalui pergumulanku di komunitas akar rumput. Pengalaman itu tentu harus aku tuliskan agar menjadi sebuah pengetahuan yang tak hanya aku miliki, tapi bisa aku bagikan kepada dunia. Seperti <em>quotes</em> yang sering aku dengar di komunitas, bahwa pengalaman perempuan adalah sumber pengetahuan.
        </p>
        <p>
          <em>Elizabeth Rona adalah anggota Mulead Lampung Perkumpulan Damar</em>
        </p>
      </div>
